.header {
  background: var(--light-color);
  background-size: cover;
  height: 90vh;
  width: 100%;
  position: relative;
  z-index: 1;
}

.container {
  max-width: 1200px;
  margin: auto;
}

.information__bar {
  background-color: var(--secondary-light-transparent-color);
  backdrop-filter: blur(5px);
}

.header .container {
  display: flex;
  padding: 0.75rem 0;
  align-items: center;
  justify-content: space-between;
}

.social__links {
  align-items: center;
  display: flex;
}
.social__link {
  margin: 1rem 0.75rem;
  cursor: pointer;
  transition: all 0.2s ease;
}
.social__link:hover {
  color: var(--primary-color);
}

.contact__info {
  margin-top: 2rem;
}

.contact__info > * {
  display: flex;
  margin: 0 1rem;
  cursor: pointer;
}

.contact__info > a > p {
  margin-left: 0.65rem;
  color: var(--dark-color);
  font-size: 0.85rem;
  letter-spacing: 0.5px;
}

.contact__info a .small-icon {
  color: var(--primary-color);
}

/* showcase styling */
.navbar {
  border-bottom: 2px solid var(--secondary-light-transparent-color);
  max-width: 1200px;
  margin: auto;
}
.navbar .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo {
  width: 100px;
  cursor: pointer;
}
.showcase__info {
  max-width: 1200px;
  margin: auto;
}

.showcase__info .container {
  color: var(--light-color);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 10rem 0;
}

.showcase__description {
  font-size: 2rem;
  color: var(--text-color);
}

.showcase__tag {
  font-size: 1.5rem;
  color: var(--text-color);
}
