.project__card {
  height: 14.7rem;
  width: 25rem;
  background: rgba(114, 114, 114, 0.5);
  position: relative;
  overflow: hidden;
  cursor: pointer;
  border-radius: 0;
  border: none;
  box-shadow: 1px 1px 2px grey;
}
.project__card:hover .hover__content {
  display: flex;
  transform: translateY(0);
  opacity: 1;
}

.project__card:hover .project__image {
  transform: scale(1.05);
  transition: all 0.5s ease;
}

.hover__content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateY(50%);
  opacity: 0;
  background: var(--primary-light-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: all 0.5s ease;
  color: var(--light-color);
  padding: 3.5rem;
}
.hover__content div {
  line-height: 2rem;
  margin-top: 0.2rem;
}
.project__image {
  width: 100%;
  height: 100%;
}
